// Proxy Plugin.
import * as actions from '../interface/actions'
import { getProxyState } from '../interface/selectors'

// Other plugins.
import { getActiveCalls } from '../../call/interfaceNew/selectors'
import { getLogManager } from '../../logs'
import { devicesChanged } from '../../webrtc/interface/actions/devices'

// Helpers.
import BasicError from '../../errors'

// Libraries.
import { call, fork, put, select, take } from 'redux-saga/effects'
import { eventChannel } from 'redux-saga'

const log = getLogManager().getLogger('PROXY')

/**
 * Sets the proxy mode for the Proxy Stack.
 * @method setProxyMode
 * @param {Object} webRTC The Proxy Stack.
 * @param {Object} action
 */
export function * setProxyMode (webRTC, action) {
  // Check to see if there are any on-going calls.
  const calls = yield select(getActiveCalls)

  if (calls.length > 0) {
    yield put(
      actions.setProxyModeFinish({
        error: new BasicError({
          code: 'proxy:3',
          message: 'Cannot change proxy mode while there are on-going calls.'
        })
      })
    )
    return
  }

  const value = action.payload.value
  const result = yield call([webRTC, 'setProxyMode'], value)

  const response = {
    value
  }
  // If the mode wasn't set, define an error to be included
  //    in the finish action.
  if (!result) {
    response.error = new BasicError({
      code: 'proxy:1',
      message: `Failed to set proxy mode to ${value}.`
    })
  }

  yield put(actions.setProxyModeFinish(response))

  // After Proxy mode is changed, manually update devices to ensure they are
  //    from the correct machine.
  const devices = yield call([webRTC.managers.devices, 'checkDevices'])
  if (devices.microphone && devices.camera && devices.speaker) {
    // Only update state with the devices if its an actual device object.
    // If the Remote SDK is not initialized yet, it will return garbage.
    yield put(devicesChanged(devices))
  }
}

/**
 * Sets the channel for the Proxy Stack.
 * @method setChannel
 * @param {Object} webRTC The Proxy Stack.
 * @param {Object} action
 */
export function * setChannel (webRTC, action) {
  // Check to see if there are any on-going calls.
  const calls = yield select(getActiveCalls)

  if (calls.length > 0) {
    yield put(
      actions.setChannelFinish({
        error: new BasicError({
          code: 'proxy:3',
          message: 'Cannot set channel while there are on-going calls.'
        })
      })
    )
    return
  }

  const channel = action.payload.channel
  const result = yield call([webRTC, 'setChannel'], channel)

  const response = {}
  // If the channel wasn't set, define an error to be included
  //    in the finish action.
  if (!result) {
    response.error = new BasicError({
      code: 'proxy:2',
      message: 'Failed to set proxy channel.'
    })
  } else {
    // Channel was successfully set.
    // Listen for messages received from the channel.
    yield fork(handleMessages, webRTC)
  }

  yield put(actions.setChannelFinish(response))
}

/**
 * Handles (non-reply) messages received from the Client Host.
 * @method handleMessages
 * @param  {Object}  webRTC The webRTC stack.
 */
export function * handleMessages (webRTC) {
  // Setup an event channel to bring the messages into redux-saga's context.
  const messageChannel = eventChannel(emit => {
    /**
     * Set the listener function on the Proxy Stack for
     *    receiving messages. It simply "emits" the entire
     *    messages into the event channel.
     */
    webRTC.onMessage((messageId, data) => {
      emit({ messageId, data })
    })

    return () => {
      // Unsubscribe by removing any listener function.
      webRTC.onMessage(() => {})
    }
  })

  while (true) {
    const { messageId, data } = yield take(messageChannel)
    log.debug(`Received message from channel: ${messageId}`)

    // Ignore messages from the channel if we're not in Proxy mode.
    const proxyState = yield select(getProxyState)
    if (!proxyState.proxyMode) {
      log.debug('Not in Proxy mode, ignoring previous message.')
      continue
    }

    // If the received message is an event, dispatch it.
    if (data.event) {
      yield put(data.event)
    } else {
      log.debug('Received unknown message type; ignoring message.', data)
    }
  }
}

export function * initializeRemote (webRTC, action) {
  const result = yield call([webRTC, 'initialize'], action.payload.config)

  if (result.error) {
    yield put(actions.initializeRemoteFinish({ error: result.error.message }))
  } else {
    yield put(actions.initializeRemoteFinish({ browser: result.browser }))
  }
}
